import * as XLSX from 'xlsx';
import DataTable from 'react-data-table-component';
import React, { useState, useEffect } from 'react';
import { Button, Form, Container, Header } from 'semantic-ui-react';
import { Link } from 'gatsby';
import axios from 'axios';
import "../css/evalForm.css";
import { stripBasename } from 'history/PathUtils';
import { createBrowserHistory } from "history";

function App() {

    const [columns, setColumns] = useState([]);
    const [data, setData] = useState([]);

    const [className, setClassName] = useState('');

    // process CSV data
    const processData = dataString => {
        const dataStringLines = dataString.split(/\r\n|\n/);
        const headers = dataStringLines[0].split(/,(?![^"]*"(?:(?:[^"]*"){2})*[^"]*$)/);

        const list = [];
        for (let i = 1; i < dataStringLines.length; i++) {
            const row = dataStringLines[i].split(/,(?![^"]*"(?:(?:[^"]*"){2})*[^"]*$)/);
            if (headers && row.length == headers.length) {
                const obj = {};
                for (let j = 0; j < headers.length; j++) {
                    let d = row[j];
                    if (d.length > 0) {
                        if (d[0] == '"')
                            d = d.substring(1, d.length - 1);
                        if (d[d.length - 1] == '"')
                            d = d.substring(d.length - 2, 1);
                    }
                    if (headers[j]) {
                        obj[headers[j]] = d;
                    }
                }

                // remove the blank rows
                if (Object.values(obj).filter(x => x).length > 0) {
                    list.push(obj);
                }
            }
        }

        // prepare columns list from headers
        const columns = headers.map(c => ({
            name: c,
            selector: c,
        }));

        setData(list);
        setColumns(columns);
    }

    // handle file upload
    const handleFileUpload = e => {
        const file = e.target.files[0];
        const reader = new FileReader();
        reader.onload = (evt) => {
            /* Parse data */
            const bstr = evt.target.result;
            const wb = XLSX.read(bstr, { type: 'binary' });
            /* Get first worksheet */
            const wsname = wb.SheetNames[0];
            const ws = wb.Sheets[wsname];
            /* Convert array of arrays */
            const data = XLSX.utils.sheet_to_csv(ws, { header: 1 });
            processData(data);
        };
        reader.readAsBinaryString(file);
    }

    const history = createBrowserHistory();

    const handleSubmit = (e) => {
        history.push("/csvsuccess");
        window.location.reload();
    }

    return (
        <>
            <Container fluid className="container">
                <Header as="h3">Please Select the Desired Class and Upload the CSV Student File.</Header>

                <Form.Field>
                    <p>Class Name - Class ID</p>
                    <select name="className" id="className" onChange={(e) => setClassName(e.target.value)}>
                        <option value="default" selected="true">Please select a class.</option>
                        <option>Corporate Entrepreneurship and Innovation</option>
                        <option>Strategy</option>
                        <option>Introduction to Organisations</option>
                        <option>Introduction to Business Analytics</option>
                        <option>Business Consulting</option>
                        <option>Family Business</option>
                        <option>Leadership Seminar with CEOs</option>
                        <option>International Business</option>
                        <option>Entrepreneurship and Business Creation</option>
                        <option>Corporate Strategy</option>
                        <option>Managing Creativity in Organizations</option>
                        <option>Sustainable Entrepreneurship</option>
                    </select>
                </Form.Field>

                <Form.Field>
                    <p>Input CSV File</p>
                    <input
                        type="file"
                        accept=".csv"
                        onChange={handleFileUpload}
                    />
                    <DataTable
                        pagination
                        highlightOnHover
                        columns={columns}
                        data={data}
                    />
                </Form.Field>

                <Button color="black" type='submit' onClick={handleSubmit}>Add Students CSV</Button>
                <Button variant="outline-light" size="lg"><Link to="/professoraddstudent">Back</Link></Button>
            </Container>
        </>
    );
}

export default App;